:root {
  --container-font-size: 16px;
  --container-background-color: ##ffffff;
  --btn-upload-background-color: #0d5eff;
  --btn-upload-color: #ffffff;
  --btn-upload-width: 180px;
  --btn-upload-height: 70px;
  --btn-upload-border-radius: 0.75em;
  --btn-upload-transition-time: 50ms;
  --progress-width: 35px;
  --progress-height: 20px;
  --progress-top: -40px;
  --progress-arrow-color: #ffffff;
  --progress-arrow-background-color: #f53060;
  --progress-arrow-width: 6px;
  --progress-arrow-height: 6px;
  --check-width: 60px;
  --check-height: 30px;
  --check-stroke: 10px;
}

#container {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-size: var(--container-font-size);
  background-color: ##ffffff;
  background-color: var(--container-background-color);
  width: 20vw;
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.btn-upload {
  color: #ffffff;
  color: var(--btn-upload-color);
  fill: #ffffff;
  fill: var(--btn-upload-color);
  font-family: inherit;
  font-size: inherit;
  font-weight: bold;
  background-color: #0d5eff;
  background-color: var(--btn-upload-background-color);
  width: 180px;
  width: var(--btn-upload-width);
  height: 70px;
  height: var(--btn-upload-height);
  padding: 0;
  border: 0;
  margin: 0;
  outline: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 0.75em;
  border-radius: var(--btn-upload-border-radius);
}

.btn-upload:not(.btn-upload-uploading) {
  transition: box-shadow 50ms ease-out,
    transform 50ms ease-out;
  transition: box-shadow var(--btn-upload-transition-time) ease-out,
    transform var(--btn-upload-transition-time) ease-out;
}

.btn-upload:not(.btn-upload-uploading):hover {
  box-shadow: 0 0.7em 1.2em rgba(25, 25, 25, 0.8);
}

.btn-upload:not(.btn-upload-uploading):active {
  box-shadow: 0 0 0 rgba(25, 25, 25, 0.8);
  transform: translateY(0.2em);
}

.btn-upload > svg {
  fill: inherit;
}

.btn-upload > span {
  color: inherit;
  margin-left: 0.3em;
}

.progress {
  color: #ffffff;
  color: var(--progress-arrow-color);
  font-family: inherit;
  font-size: 50%;
  background-color: #f53060;
  background-color: var(--progress-arrow-background-color);
  width: 35px;
  width: var(--progress-width);
  height: 20px;
  height: var(--progress-height);
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  position: absolute;
  left: 0;
  top: -40px;
  top: var(--progress-top);
  border-radius: 0.5em;
  opacity: 0;
  transform-origin: center bottom;
  transform: translateX(-50%);
  -webkit-filter: drop-shadow(0 0.7em 1.2em rgba(25, 25, 25, 0.5));
          filter: drop-shadow(0 0.7em 1.2em rgba(25, 25, 25, 0.5));
}

.progress:after {
  content: "";
  border-top: solid 6px
    #f53060;
  border-top: solid var(--progress-arrow-height)
    var(--progress-arrow-background-color);
  border-bottom: 0;
  border-left: solid calc(6px / 2) transparent;
  border-left: solid calc(var(--progress-arrow-width) / 2) transparent;
  border-right: solid calc(6px / 2) transparent;
  border-right: solid calc(var(--progress-arrow-width) / 2) transparent;
  position: absolute;
  top: calc(100% - 1px);
}

.check {
  width: 60px;
  width: var(--check-width);
  height: 30px;
  height: var(--check-height);
  display: none;
  position: absolute;
  top: calc(50% - calc(60px / 2));
  top: calc(50% - calc(var(--check-width) / 2));
  left: calc(50% - calc(30px / 2));
  left: calc(50% - calc(var(--check-height) / 2));
  transform-origin: center bottom;
  transform: rotate(-45deg);
}

.check > span {
  background-color: #ffffff;
  display: block;
  position: absolute;
}

.check > span:first-child {
  width: 10px;
  width: var(--check-stroke);
  height: 0;
  top: 0;
  left: 0;
}

.check > span:last-child {
  width: 0;
  height: 10px;
  height: var(--check-stroke);
  bottom: 0;
  left: 0;
}

